.search{
    display: flex;
    align-items: center;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px #000;
    margin: 20px;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    
}


.detail_contact {
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px #000;
    margin: 20px;
    font-size: 30px;
    align-content: flex-start;
    background: rgb(174,196,238);
    background: radial-gradient(circle, rgba(174,196,238,1) 0%, rgba(214,218,223,1) 100%);
}


